class Zone {

    /**
     * @returns {boolean}
     */
    canExplore() {
        return this.game.mode == 'home';
    }

    /**
     *
     */
    explore() {
        if (!this.canExplore()) return;
        var oppoents = _.sample(this.opponents(), 1)[0];
        this.game.battle = new Battle(this.game, opponents);
    }
}